import React, { Component } from "react";
//form elements
import FormBuilderInput from "./elements/input";

class FormBuilder extends Component {
    state = {
        formElements: [{}],
        inputCount: 0
    }

    addInput = () => {
        let current = this.state.inputCount
        current++
        this.setState({
            inputCount: current
        });
    }
    removeInput = () => {
        let current = this.state.inputCount
        current--
        if (current < 0){current = 0}
        this.setState({
            inputCount: current
        });
    }

    render(){
        return (
            <form className="creator">
            {this.state.inputCount.map(index => { 
                return <FormBuilderInput />;
            })}
                <a onClick={this.addInput}>[Add Input]</a>
                <a onClick={this.removeInput}>[Remove Input]</a>
            </form>
        )
    }
}
export default FormBuilder;